
import React, { useCallback, useEffect, useState } from 'react';
import { IconTextInput } from '../../components/TextInput';
import PencilIconSvg from '../../assets/icon/pencil.svg';

import { styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import MenuIcon from '@mui/icons-material/Menu';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';

import ToggleOffTwoToneIcon from '@mui/icons-material/ToggleOffTwoTone';
import ToggleOnTwoToneIcon from '@mui/icons-material/ToggleOnTwoTone';

import IconButton from '@mui/material/IconButton';

import { green, pink, deepOrange } from '@mui/material/colors';
import { Button } from '@mui/material';

import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';

import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import YardOutlinedIcon from '@mui/icons-material/YardOutlined';

import PropTypes from 'prop-types';

import CloseIcon from '@mui/icons-material/Close';


import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import PersonIcon from '@mui/icons-material/Person';
import AddIcon from '@mui/icons-material/Add';
import { blue } from '@mui/material/colors';

import Popover from '@mui/material/Popover';
import TextField from '@mui/material/TextField';
import { useNavigate } from 'react-router-dom';
import GoogleMapScreen from "../GoogleMap/";
import { width } from '@mui/system';
import Popper from '@mui/material/Popper';

let opa = 0;

function Menu(props) {
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [anchorEl2, setAnchorEl2] = React.useState(null);
    const [ppanchorEl, ppsetAnchorEl] = React.useState(null);


    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
        opa = 0;
    };

    const handleClose = () => {
        setAnchorEl(null);
        setAnchorEl2(null);
        ppsetAnchorEl(null);
        opa = 0;
    };

    const exit = () => {
        setAnchorEl(null);

        setTimeout(() => {
            setAnchorEl2(null);
            ppsetAnchorEl(null);
            opa = 0;
          }, 500);
    };

    const open = Boolean(anchorEl);
    const id = open ? 'simple-popover' : undefined;


    //
    const phandleClick = (event) => {
        setAnchorEl2(event.currentTarget);
        setAnchorEl(event.currentTarget);
    };

    const phandleClose = () => {
        setAnchorEl2(null);
        setAnchorEl(null);
        ppsetAnchorEl(null);
    };


    const navigate = useNavigate();


    function returnNFTList() {
        props.DeleteScene();
        localStorage.removeItem('item2')

        window.location.replace("/mypage")
        // setTimeout(() => {
        //     navigate(`/mypage`);
        //   }, 1500);

    }


    const pphandleClick = (event) => {


        setAnchorEl(null);
        setAnchorEl2(null);
        ppsetAnchorEl(null);

        ppsetAnchorEl(event);


        setTimeout(() => {
            opa = 1;
          }, 50);


        setTimeout(() => {
            setAnchorEl(event);
          }, 100);



        console.log("3333333")
        console.log("333333333")
    };3

    const ppopen = Boolean(ppanchorEl);
    const ppid = ppopen ? 'simple-popper' : undefined;


    const popen = Boolean(anchorEl2);
    const pid = popen ? 'simple-popover' : undefined;
    


    return (

        <div >
            <IconButton
                edge="start"
                color="inherit" onClick={handleClick}


                >


                <MenuIcon fontSize="large" />
            </IconButton>

            <Dialog onClose={handleClose} open={open} >
                <DialogTitle fontSize={26} textAlign='center'

                    sx={{ pt: '20px', marginBottom: '-50px' }}>MENU
                            
                    </DialogTitle>

                <Grid
                    container
                    direction="row"
                    justifyContent="center"
                    alignItems="center"
                >

                    <Button variant="contained" aria-describedby={id} onClick={pphandleClick}
                        sx={{ 
                            pt: '10px', pb: '10px', width: '60%',
                             marginTop: '50px', marginBottom: '30px' }}>

                        OPEN EXHIBITION MAP
                    </Button>


                    <Popper
                        id={ppid} open={ppopen} anchorEl={ppanchorEl}
                        placement='bottom'

                        PaperProps={{
                            style: { width: '100%', height: '100%',    zIndex: 1000000 },
                        }}

                        style={{zIndex: 10000,
                            opacity: opa
                        }} 
                    >

                      <AppBar position="fixed"
                                style={{ background: '	#483D8B'}}

                            >
                                <Toolbar variant="dense">
                                    <YardOutlinedIcon
                                     fontSize="large" edge="start" 
  
                                     />
                                    <Typography variant="h6" align='center' 
                                    sx={{ flexGrow: 1 }} >
                                        SEED EXHIBITION
                                    </Typography>

                                    <IconButton

                                        edge="end"
                                        color="inherit" onClick={exit}

                                        >        
                                        <CloseIcon fontSize="large" 
                                        />
                                    </IconButton>
                                </Toolbar>
                            </AppBar>      
                        <GoogleMapScreen  {...props} />
                    </Popper >
                    <Button variant="contained" onClick={returnNFTList}
                        sx={{
                            pt: '10px', pb: '10px', width: '60%', marginBottom: '50px'
                            }}>

                        RETURN NFT LIST
                    </Button>
                </Grid>
            </Dialog>
        </div>
    )
}

export default Menu;