import React from 'react';
import { styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import ToggleOffTwoToneIcon from '@mui/icons-material/ToggleOffTwoTone';
import ToggleOnTwoToneIcon from '@mui/icons-material/ToggleOnTwoTone';
import IconButton from '@mui/material/IconButton';
import { deepOrange } from '@mui/material/colors';
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import LoadingButton from '@mui/lab/LoadingButton';
import YardOutlinedIcon from '@mui/icons-material/YardOutlined';
import Privacy from '../Renderer/Privacy';

const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
}));

function MainPresenter(props) {

    const [loading, setLoading] = React.useState();
    const [loading_Test, setLoading_Test] = React.useState();
    function Loading() {
        setLoading(true);
    }
    function Loading_Test() {
        setLoading_Test(true);
    }

    return (


        <Box

            sx={{
                width: '100%',
                marginBottom: '6%'

            }}>
            <Grid container
                direction="column"
                alignItems="center"
                justifyContent="center"
            >
                <AppBar position="static"  >
                    <Toolbar variant="dense">

                        <YardOutlinedIcon fontSize="large" />
                        <Typography variant="h6" align='center' sx={{ flexGrow: 1 }} >
                            WALLET CONNECT


                        </Typography>
                        {/* <IconButton

                            edge="end"
                            color="inherit"

                        >
                            <AccountCircleIcon fontSize="large" />
                        </IconButton> */}
                        <Privacy {...props}/>

                    </Toolbar>
                </AppBar>


                <Grid marginTop={'3%'}>
                    <img src={process.env.PUBLIC_URL + "/img/wallet.png"} width="300" margin="30" ></img>

                </Grid>

                <Grid item  width='70vw' marginTop={'3%'} marginBottom={'6%'} boxShadow={8}>

                    <Item >
                        <List>

                            <ListItem >
                                <ListItemAvatar  >
                                    <Avatar sx={{ bgcolor: deepOrange[100], width: 48, height: 48 
                                    ,marginLeft:'-12%'
                                    }}>


                                        {props.active ? <IconButton onClick={props.disconnect}><ToggleOnTwoToneIcon color="primary" fontSize='large' /></IconButton> : <IconButton disabled={(props.active_Test)} onClick={props.connectWalletConnect}><ToggleOffTwoToneIcon color="error" fontSize='large' /></IconButton>}

                                    </Avatar>

                                </ListItemAvatar >
                                <ListItemText   primary="ACTIVE / ACCOUNT" secondary=
                                    {props.active ? "Connect / " + props.account.substr(0, 6) + "..." : "Not Connect"}
                                />
                            </ListItem>

                            <Box
                                display="grid"
                                alignItems="center"
                                justifyContent="center"
                                margin="4%"
                            >
                                {/* <Button variant="contained" 
                                disabled={!props.active}
                                 onClick={() => { props.walletSearch(); Loading(); }} loading={loading}
                                >WALLET SEARCH</Button> */}

                                <LoadingButton variant="contained" sx={{width:'50vw' ,height:'100%', fontSize:'18px'}}
                                disabled={!props.active}
                                onClick={() => { props.walletSearch(); Loading(); }} loading={loading}
                                >WALLET SEARCH</LoadingButton>

                            </Box>
                        </List>
                    </Item>
                </Grid>
                <Grid item  width='70vw'  boxShadow={8}>
                    <Item >
                        <List>
                            <ListItem >
                                <ListItemAvatar >
                                    <Avatar sx={{ bgcolor: deepOrange[100], width: 48, height: 48
                                                                        ,marginLeft:'-12%' }}>
                                        {props.active_Test ? <IconButton onClick={props.disconnect_Test}><ToggleOnTwoToneIcon color="primary" fontSize='large' /></IconButton> : <IconButton disabled={(props.active)} onClick={props.connectWalletConnect_Test}><ToggleOffTwoToneIcon color="error" fontSize='large' /></IconButton>}
                                    </Avatar>
                                </ListItemAvatar >
                                <ListItemText primary="TEST CONNECT" secondary=
                                    {props.active_Test ? "Connect / " + props.account_Test.substr(0, 6) + "..." : "Not Connect"}
                                />
                            </ListItem>
                            <Box
                                display="grid"
                                alignItems="center"
                                justifyContent="center"
                                margin="4%"
                            >
                                <LoadingButton variant="contained"   sx={{width:'50vw' ,height:'100%', fontSize:'18px'}}
                                disabled={!props.active_Test}
                                onClick={() => { props.walletSearch_Test(); Loading_Test(); }} loading={loading_Test}
                                >WALLET SEARCH</LoadingButton>

                            </Box>
                        </List>
                    </Item>
                </Grid>
            </Grid>
        </Box >

    );
}

export default MainPresenter;
