import React, { useState } from 'react';
import MypagePresenter from './MypagePresenter';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';

/**
 *   ,    
 */


function MypageContainer({ user, setUser}) {

   // walletAddress = localStorage.getItem('L_address')
   // NFTArray = JSON.parse(localStorage.getItem('L_arr'))

   if(localStorage.getItem('item'))
   {
    let getitem = JSON.parse(localStorage.getItem('item'))
    user = getitem.value
   }

                

    const navigate = useNavigate();

    const props = {
        user,
        setUser,
        onClickTitleButton: (nft) => {
            const newnft = nft

          //  localStorage.setItem('L_nft', nft);



            setUser((prevState) => {
                return { ...prevState, user_NFT: nft}
            });


            navigate(`/renderer`);

        },
    }

    return (
        <>
            <MypagePresenter {...props} />
        </>
    );
}

export default MypageContainer;