
import React, { useCallback, useEffect, useState } from 'react';
import { IconTextInput } from '../../components/TextInput';
import PencilIconSvg from '../../assets/icon/pencil.svg';

import { styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';

import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';

import ToggleOffTwoToneIcon from '@mui/icons-material/ToggleOffTwoTone';
import ToggleOnTwoToneIcon from '@mui/icons-material/ToggleOnTwoTone';

import IconButton from '@mui/material/IconButton';

import { green, pink, deepOrange } from '@mui/material/colors';
import { Button } from '@mui/material';

import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';

import AccountCircleIcon from '@mui/icons-material/AccountCircle';
import YardOutlinedIcon from '@mui/icons-material/YardOutlined';

import PropTypes from 'prop-types';


import DialogTitle from '@mui/material/DialogTitle';
import Dialog from '@mui/material/Dialog';
import PersonIcon from '@mui/icons-material/Person';
import AddIcon from '@mui/icons-material/Add';
import { blue } from '@mui/material/colors';

import Popover from '@mui/material/Popover';
import TextField from '@mui/material/TextField';

const emails = ['username@gmail.com', 'user02@gmail.com'];
//let nickName = "none"
let NFTInformation = "none"

function SimpleDialog(props) {
    const { onClose, selectedValue, open } = props;
    const [anchorEl, setAnchorEl] = React.useState(null);

    const handleClose = () => {
        onClose(selectedValue);
    };
    
    //
    const phandleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const phandleClose = () => {
        setAnchorEl(null);
    };


    const popen = Boolean(anchorEl);
    const id = popen ? 'simple-popover' : undefined;

    let name;
    
    function inputName(e) {
        console.log(e.target.value)
        name = e.target.value;
    }
    function setName() {
        console.log(name)
        console.log(props)


        props.setNickName(name)

    }

    return (


        <Dialog onClose={handleClose} open={open} >
            <DialogTitle fontSize={26}  textAlign='center' 
                sx={{ pt: '16px', pb: '40px' , width:'180px'}}>PRIVACY</DialogTitle>
            <Grid container
                direction="column"
                alignItems="center"
                justifyContent="center"
            >
                <div>
                    <Button variant="outlined" aria-describedby={id} onClick={phandleClick}
                    disabled = {!props.user.user_NFT}
                        sx={{ pt: '6px', pb: '6px', marginTop:'-30px' }}>

                        EDIT NAME
                    </Button>
                    <Popover
                        id={id}
                        open={popen}
                        anchorEl={anchorEl}
                        onClose={phandleClose}
                        anchorOrigin={{
                            vertical: 'center',
                            horizontal: 'center',
                        }}
                    >
                        <Box
                            component="form"
                            // sx={{
                            //     '& > :not(style)': { m: 1, width: '25ch' },
                            // }}
                            // noValidate
                            // autoComplete="off"
                            display="flex"
                        >
                            <TextField sx={{ m: 1, p: 0 }}
                                id="outlined-basic" label="Input NFT Name" variant="outlined" onChange={inputName} />
                            <Button variant="contained"  onClick={() => { setName(); phandleClose(); }} sx={{ m: 1 }}>

                                SET
                            </Button>
                        </Box>
                    </Popover>

                </div>
            </Grid>
            {/* 
            <Button variant="outlined" textAlign="center" sx={{ mt: '20px', ml: '20px',mr: '20px' ,pt: '6px',pb: '6px' }}>EDIT NAME</Button> */}

            <List >



                <ListItem >



                </ListItem>

                <ListItem sx={{ pt: '0px', pb: '0px' }}>

                    <ListItemText primary="NFT-NAME"
                        primaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '600',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                        secondary={props.user.user_NFTNickName ? props.user.user_NFTNickName : "Not Connect"}
                        secondaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '100',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                    />

                </ListItem>


                <ListItem >
                    <ListItemText primary={"NFT-TITLE"}
                        primaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '600',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                        secondary={props.user.user_NFT ? props.user.user_NFT.title : "Not Connect"}
                        secondaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '100',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                    />
                </ListItem>
                <ListItem >
                    <ListItemText primary={"NFT-TOKEN-ID"}
                        primaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '600',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                        secondary={props.user.user_NFT ? props.user.user_NFT.tokenId : "Not Connect"}
                        secondaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '100',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                    />
                </ListItem>
                <ListItem >
                    <ListItemText primary={"NFT-TOKEN-TYPE"}
                        primaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '600',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                        secondary={props.user.user_NFT ? props.user.user_NFT.tokenType : "Not Connect"}
                        secondaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '100',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                    />
                </ListItem>


                <ListItem>

                    <ListItemText primary="ACCOUNT"
                        primaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '600',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                        secondary={props.user.user_Active ? props.user.user_Account.substr(0, 6) + "..." : "Not Connect"}
                        secondaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '100',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                    />

                </ListItem>
            </List>


        </Dialog>

    );
}











SimpleDialog.propTypes = {
    onClose: PropTypes.func.isRequired,
    open: PropTypes.bool.isRequired,
    selectedValue: PropTypes.string.isRequired,
};


function Privacy(props) {
    const [open, setOpen] = React.useState(false);
    const [selectedValue, setSelectedValue] = React.useState(emails[1]);

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = (value) => {
        setOpen(false);
        setSelectedValue(value);
    };


    function showPrivacy() {
        console.log(props.account_Test);
    }



    return (
        <>
            <IconButton
                edge="end"
                color="inherit" onClick={handleClickOpen}
>
                <AccountCircleIcon fontSize="large" />
            </IconButton>
            <SimpleDialog {...props}
                selectedValue={selectedValue}
                open={open}
                onClose={handleClose}
            />

        </>
    );
}

export default Privacy;