import React, { useEffect, useState } from 'react';
import { GoogleMap, useJsApiLoader, Marker } from '@react-google-maps/api';
import { Button } from '@mui/material';
import { styled } from '@mui/material/styles';
import MarkerImg from './pin.png';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import FormControl from '@mui/material/FormControl';
import ArrowForwardOutlinedIcon from '@mui/icons-material/ArrowForwardOutlined';
import ClickAwayListener from '@mui/material/ClickAwayListener';
import InputLabel from '@mui/material/InputLabel';
import Select from '@mui/material/Select';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListSubheader from '@mui/material/ListSubheader';

import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import RemoveCircleTwoToneIcon from '@mui/icons-material/RemoveCircleTwoTone';
import AddCircleTwoToneIcon from '@mui/icons-material/AddCircleTwoTone';
import IconButton from '@mui/material/IconButton';
import ButtonGroup from '@mui/material/ButtonGroup';
import { width } from '@mui/system';



const JE_Position = {
  lat: 33.396514,
  lng: 126.344930
}

const YO_Position = {
  lat: 34.750594,
  lng: 127.745762
}

const GA_Position = {
  lat: 37.791855,
  lng: 128.90720,
}

let firstShowRoom = false;
let names = []
let a = 1;
const containerStyle = {

  width: '100vw',
  height: '100vh',
};


let curZoom = 3;
let showRoomArr = [];
let showRoomEndArr = [];

function showRoom() {
  let name, position, dateStart, dateEnd, imgWeightSize;
}
//state 0=coming 1=now 2=end
function new_ShowRoom(_name, _position, _dateStart, _dateEnd, _imgWeightSize) {

  let room = new showRoom();
  room.name = _name;
  room.position = _position;
  room.dateStart = _dateStart;
  room.dateEnd = _dateEnd;
  room.imgWeightSize = _imgWeightSize;
  return room;

}

const NONE_Position = {

  lat: 36.594678,
  lng: 127.985756

}


let coming, currerent, end

let none = new_ShowRoom("NONE", NONE_Position, "NONE", "NONE", "200");


function addcomingArr(_name, _position, _dateStart, _dateEnd, _imgWeightSize) {
  let room = new_ShowRoom(_name, _position, _dateStart, _dateEnd, _imgWeightSize);
  coming = room;
  firstShowRoom = true;
}
function addnowArr(_name, _position, _dateStart, _dateEnd, _imgWeightSize) {
  let room = new_ShowRoom(_name, _position, _dateStart, _dateEnd, _imgWeightSize);
  currerent = room;
  firstShowRoom = true;
}
function addEndArr(_name, _position, _dateStart, _dateEnd, _imgWeightSize) {
  let room = new_ShowRoom(_name, _position, _dateStart, _dateEnd, _imgWeightSize);
  end = room;
  showRoomEndArr.push(room)
  firstShowRoom = true;
}
function removecomingArr() {
  coming = null;
}
function removenowArr() {
  currerent = null;
}

function removeEndArr() {
  end = null;
}





