function GoogleMapPresenter(props) {
  const { isLoaded } = useJsApiLoader({
    id: 'google-map-script',
    googleMapsApiKey: "AIzaSyDAR5IsWK28BMFmf7TBPcToCG7sQoO81aA"
  })




  coming = props.user.user_NFTComing
  currerent = props.user.user_NFTNow
  end = props.user.user_NFTEnd


  names = []
  showRoomEndArr = []
  showRoomEndArr = props.user.user_ShowRoomEndArr





  // const getitem = JSON.parse(localStorage.getItem('item'))

  // props.setUser(getitem)

  // console.log(getitem)
  // console.log(props.user)

  for (let i = 0; i < showRoomEndArr.length; ++i) {
    let a = showRoomEndArr[i].dateStart + " ~ " + showRoomEndArr[i].dateEnd + " - " + showRoomEndArr[i].name
    names.unshift(a)
  }







  if (!coming && !currerent && !end) {
    firstShowRoom = false;
  }
  else {
    firstShowRoom = true;
  }

  const [map, setMap] = React.useState(null)

  const onLoad = React.useCallback(function callback(map) {

    // This is just an example of getting and using the map instance!!! don't just blindly copy!
    loadMap();
    const bounds = new window.google.maps.LatLngBounds(pick() ? pick().position : null);
    map.fitBounds(bounds);

    setMap(map)
  }, [])

  const onUnmount = React.useCallback(function callback(map) {
    setMap(map)
  }, [])




  const onLoad2 = marker => {
    console.log('marker: ', marker)
  }


  async function loadMap() {
    bhandleToggle()

    setTimeout(() => {
      bhandleClose()
    }, 600);

  }

  const [zoom, setZoom] = useState()


  useEffect(() => {
    setTimeout(() => {
      setZoom(3)
    }, 800);
  }, [])


  const MarkerClick = (p) => {

    loadMap()
    map.setCenter(p);
   // setZoom(3)
   setZoom(curZoom)

    setTimeout(() => {
      setZoom(15)
    }, 400);

  }


  var myOptions = {
    panControl: false,
    zoomControl: false,
    scaleControl: false,
    streetViewControl: false,
    mapTypeControl: false,
    overviewMapControl: false,
    rotateControl: false,
    disableDefaultUI: true,
    clickableIcons: false,
  }

  const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  }));

  const [open, setOpen] = React.useState(false);

  const handleClick = () => {
    setOpen((prev) => !prev);
  };

  const handleClickAway = () => {
    setOpen(false);
  };

  const styles = {
    position: 'fixed',
    top: '30vh',
    right: 0,
    left: 0,
    zIndex: 1,
    border: '1px solid',
    p: 2,
    borderRadius: 2,
    margin: 2,
    bgcolor: 'background.paper',
  };





  const [personName, setPersonName] = React.useState([]);
  const handleChangeMultiple = (event) => {
    const { options } = event.target;
    const value = [];
    for (let i = 0, l = options.length; i < l; i += 1) {
      if (options[i].selected) {
        value.push(options[i].value);
      }
    }
    setPersonName(value);
  };


  const NONE_Position = {

    lat: 36.594678,
    lng: 127.985756

  }

  const [bopen, setbOpen] = React.useState(false);
  const bhandleClose = () => {
    setbOpen(false);
  };
  const bhandleToggle = () => {
    setbOpen(!open);
  };


  function PlusZoom() {

    let a = curZoom + 1;
    setZoom(a)
  }
  function SuntractZoom() {
    let a = curZoom - 1;
    setZoom(a)
  }

  function handleZoomChanged() {
    console.log(this.getZoom())
    curZoom = this.getZoom()
  }



  return isLoaded ? (


    <>
      <div>
        <Backdrop
          sx={{ color: '#fff', height: '100vh', zIndex: (theme) => theme.zIndex.drawer + 1 }}
          open={bopen}
        // onClick={bhandleClose}
        >
          <CircularProgress color="inherit" >

          </CircularProgress>
        </Backdrop>
      </div>


      <Box
      >
        <Grid
          sx={{ height: '6vh ' }}
        >
          <GoogleMap
            onZoomChanged={handleZoomChanged}
            mapContainerStyle={containerStyle}

            onLoad={onLoad}
            onUnmount={onUnmount}
            zoom={zoom}
            options={myOptions}
            center={pick().position}

          >
            {
              !firstShowRoom ?

                <Marker
                  optimized={false}
                  zIndex={5}
                  label={{ src: { MarkerImg }, text: "have no plans", color: '#000', fontSize: "20px", fontWeight: "500" }}
                  onLoad={onLoad2}

                  position={currerent ? currerent.position : NONE_Position}

                  icon={{
                    url: MarkerImg,

                    anchor: new google.maps.Point(90, 60),
                    scaledSize: new google.maps.Size(180, 180)
                  }}
                />


                : null
            }
            {
              currerent ?
                <Marker

                  optimized={false}
                  zIndex={5}
                  label={{ src: { MarkerImg }, text: currerent ? "NOW" : "cNONE",
                   color: '#000', fontSize: "12px", fontWeight: "600" }}
                  onLoad={onLoad2}

                  position={currerent ? currerent.position : NONE_Position}
                  onClick={() => MarkerClick(currerent.position)}
                  icon={{
                    url: MarkerImg,

                    anchor: new google.maps.Point(30, 40),
                    scaledSize: new google.maps.Size(60, 120)
                  }}
                /> : null
            }


            {
              coming ?
                <Marker
                  optimized={false}
                  zIndex={4}
                  label={{ src: { MarkerImg }, text: coming ? "COMING" : "cNONE", color: '#000', 
                  fontSize: "12px", fontWeight: "600" }}
                  onLoad={onLoad2}
                  position={coming ? coming.position : NONE_Position}

                  onClick={() => MarkerClick(coming.position)}
                  icon={{
                    url: MarkerImg,

                    anchor: new google.maps.Point(40, 40),
                    scaledSize: new google.maps.Size(80, 120)
                  }}
                /> : null
            }


            {
              end ?
                <Marker
                  optimized={false}
                  zIndex={5}
                  label={{ src: { MarkerImg }, text: end ? "ENDED" : "eNONE", color: '#000', fontSize: "12px", fontWeight: "600" }}
                  onLoad={onLoad2}
                  position={end ? end.position : NONE_Position}
                  onClick={() => MarkerClick(end.position)}

                  icon={{
                    url: MarkerImg,


                    anchor: new google.maps.Point(30, 38),
                    scaledSize: new google.maps.Size(60, 120)
                  }}
                /> : null
            }

          </GoogleMap>

        </Grid>
        <>
          <Grid container
            direction="row"
            alignItems="center"
            justifyContent="center"
          >


            <Grid >
              <Button variant="contained"
                sx={{padding:'4%'}}
                style={{ marginTop: "40%", width: "90px", 
                display: "grid", borderRadius: 8, }}
                onClick={() => coming ? MarkerClick(coming.position) : null} >
                <Typography
                  fontSize="10px">
                  COMING
                </Typography>
                <Typography
                                sx={{marginTop:'-10%'}}
                  fontSize="10px" fontWeight={"100"}>
                  {coming ? coming.name : "NONE"}
                </Typography>
              </Button>
            </Grid>

            <Grid item mr={'2vw'} ml={'2vw'} >

            </Grid>
            
            <Grid item >
              <Button variant="contained"
                              sx={{padding:'4%'}}
                style={{ marginTop: "40%", width: "90px", 
                display: "grid", borderRadius: 8, }}
                onClick={() => currerent ? MarkerClick(currerent.position) : null} >
                <Typography
                  fontSize="10px">
                  NOW
                </Typography>
                <Typography
                sx={{marginTop:'-10%'}}
                  fontSize="10px" fontWeight={"100"}>
                  {currerent ? currerent.name : "NONE"}
                </Typography>
              </Button>


            </Grid>



            <Grid item mr={'2vw'} ml={'2vw'} >
             

            </Grid>

            <Grid item >
              <Button variant="contained" color="primary"
                              sx={{padding:'4%'}}
                style={{ marginTop: "40%", width: "90px", 
                display: "grid", borderRadius: 8, }}
                onClick={() => end ? MarkerClick(end.position) : null} >
                <Typography
                  fontSize="10px">
                  ENDED
                </Typography>
                <Typography
                                sx={{marginTop:'-10%'}}
                  fontSize="10px" fontWeight={"100"}>
                  {end ? end.name : "NONE"}
                </Typography>
                
              </Button>
            </Grid>
          </Grid>








          <Grid
            container
            direction="row"
            justifyContent="center"

          >

            <ClickAwayListener onClickAway={handleClickAway} >
              <Box marginTop='1vmin'>
                <Button onClick={handleClick} variant="contained" color="primary"
                  style={{
                    width: "140px",
      


                    borderRadius: 10,

                    fontSize: "10px",

                  }}

                >
                  <Typography
                    fontSize="12px">
                    PAST SHOW LIST
                  </Typography>

                </Button>


                  <IconButton
                color="primary" onClick={PlusZoom}        
                sx={{paddingLeft:'40px' }}>
                <AddCircleTwoToneIcon fontSize="large" />
            </IconButton>

           <IconButton
                color="primary" onClick={SuntractZoom} 
                                  sx={{ }}>
                <RemoveCircleTwoToneIcon fontSize="large" />
            </IconButton>








                {open ? (



                  <Grid item

                    container
                    direction="column"
                    alignItems="center"


                  >


r

                    <List
                      sx={{
                        width: 'center',
                        height: 'center',
                        position: 'fixed',
                        maxHeight: '30%',
                        bgcolor: 'background.paper',

                        overflow: 'auto',
                        marginTop: '4%',
                        borderRadius: 2,
                        '& ul': { padding: 0 },
                      }}
                      subheader={<li />}
                    >
                      {[0].map((sectionId) => (
                        <li key={`section-${sectionId}`}>
                          <ul>
                            {/* <ListSubheader
sx={{textAlign:'center', fontSize:'20px', fontWeight: '600' ,marginTop: '4%'}}
            >{`ENDED`}</ListSubheader> */}
                            {names.map((item) => (
                              <ListItem key={`${sectionId}-${item}`}
                              >
                                <ListItemText primary={` ${item}`}
                                  primaryTypographyProps={{
                                    fontSize: 11,
                                    fontWeight: '400',
                                    textAlign: 'center',
                                    marginBottom: '-7px'
                                  }}

                                />
                              </ListItem>
                            ))}
                          </ul>
                        </li>
                      ))}
                    </List>


                    {/* <ListItemText primary="ACCOUNT"
                        primaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '600',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                        secondary={props.user.user_Active ? props.user.user_Account.substr(0, 6) + "..." : "Not Connect"}
                        secondaryTypographyProps={{
                            fontSize: 14,
                            fontWeight: '100',
                            mb: '2px',
                            textAlign: 'center',
                            pt: '0px', pb: '0px'
                        }}
                    /> */}



                  </Grid>


                ) : null}

              </Box>

            </ClickAwayListener>

          </Grid>

























        </>
      </Box>
    </>

  ) : <></>
}

export default React.memo(GoogleMapPresenter)